
.post{
  border-bottom: 0.1em solid #ddd;
  border-top: 0.1em solid #ddd;
  padding: 0.3em;
  margin: 0.3em;
}
.post > h2:hover{
  background-color: #eee;
  cursor:pointer;
}
.post > h2 {
  margin: 0.1em;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s
}
.fade-enter, .fade-leave-to /* .fade-leave-active in <2.1.8 */ {
  opacity: 0
}



